.main{
    position: absolute;
    width: 85vw;
    height: 100%;
    right:0;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    justify-content: flex-end; 

    background-color: white;
}
/* =========================================================== */
.topbox{
    position: relative;
    display: flex;
    width: 80%;
    height: 60%;

    margin-bottom: 50px;
    
    background-color: red;

    box-shadow: 4px 4px 10px rgb(131, 0, 0);
}

/* ============================2 */
.top_leftbox{
    position: relative;

    width: 40%;
    height: 100%;
    background-color: pink;

    display: flex;
    justify-content: center;
}

.backbutton{
    position: absolute;
    top :20px;
    left: 20px;
    
    width: 50px;
    height: 50px;

    display: flex;
    justify-content: center;
    align-items: center;

    border: none;
    border-radius: 50%;
    background-color: rgb(252, 174, 174);

    cursor: pointer;
}

.imgbox{
    position: relative;

    width : 40%;
    height: 45%;
    
    top: 20%;

    border-radius: 6px;

    background-image: url('./img/1.jpg') ;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.buttonbox{
    position: absolute;
    width : 100%;
    height : 30%;

    bottom: 0;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 100px;

    /* background-color: white; */
}

.modify_button{
    width: 20%;
    height: 30%;

    border: none;
    border-radius: 5px;

    background-color: rgb(252, 174, 174);

    cursor: pointer;
}

.delete_button{
    width: 20%;
    height: 30%;
    
    border: none;
    border-radius: 5px;

    background-color: rgb(252, 174, 174);
}
/* ============================2 */

.top_rightbox{
    width: 60%;
    height: 100%;
    background-color: pink;

    justify-items: center;

    cursor: pointer;
}

.inputboxes{
    position: relative;
    width: 100%;
    height: 60%;

    display: grid;
    grid-template-columns: repeat(2, 1fr); /* 2열 정렬 */
    gap: 20px; /* 요소 간 간격 */

    justify-items: center;
    align-content: center;

}

/* =========================================================== */

.bottombox{
    position: relative;
    
    width: 80%;
    height: 30%;

    background-color: pink;

    border-radius: 5px 5px 0 0;
    
    box-shadow: 4px 4px 10px rgb(131, 0, 0);;
}