.name_box{
    width: 100%;

    justify-items: center;

}

.inputboxname{
    width: 200px;
    height: 50px;

    align-content: center;

    font-size: 20px;
}

.input_container{
    position: relative;
    width: 200px;
    height: 50px;

    /* background-color: black; */
}

.input_container::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 15px;                       /*구분과 선의 줄*/
    width: 100%;
    height: 2px;
    background-color: orangered;
}

.inputbox{
    background: transparent;
    border: none;  
    outline: none; 
    color: white;  /* 입력된 텍스트 색상 */
}