.main {
    position: relative;
    width: 85vw;
    height: 100vh;
    /* background-color: rgb(136, 194, 255); */
    padding: 12.5vw;
    box-sizing: border-box;
}

.title {
    position: absolute;
    top: 10%;
    filter: drop-shadow(0 4px 4px rgba(0, 0, 0, 0.25));
    font-size: 36px;
    font-weight: bold;
}

.btns {
    display: flex;
    position: relative;
}

.registerBtn {
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 7px;
    padding: 10px 20px;
    cursor: pointer;
}

.registration {
    transition: all 300ms ease-in-out;
}

.registration-enter {
    opacity: 0;
    transform: scale(0.5);
}

.registration-enter.registration-enter-active {
    opacity: 1;
    transform: scale(1);
}

.registration-leave {
    opacity: 1;
    transform: scale(1);
}

.registration-leave.registration-leave-active {
    opacity: 0;
    transform: scale(0.5);
}

.searchContainer {
    display: flex;
    margin-left: auto;
}

.search {
    display: flex;
    position: relative;
}

.input,
.dropdown {
    border-radius: 7px;
    padding: 10px;
    margin-left: 20px;
    border: 1px solid #ddd;
}

.input {
    width: 300px;
}

.icon {
    position: absolute;
    top: 50%;
    right: 5px;

    transform: translateY(-50%);

    background-color: transparent;
    border: none;
    cursor: pointer;

    color: black;
    font-size: 1rem;
}


.tableHeader {
    display: flex;
    position: relative;
    padding: 20px 0 20px 5px;
    margin: 30px 0 10px;
    background-color: #ddd;
    justify-content: space-evenly;

    font-weight: 600;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.tableHeader div {
    width: calc(100% / 7);
    text-align: center;
}

.tableBody {
    table-layout: fixed;
    border-collapse: separate;
    width: 100%;
    text-align: center;
    border-spacing: 0 20px;

    /* background-color: #fff; */
}

.tableBody tr {
    position: relative;
    height: 70px;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.2);
    border: 1px solid #ddd;
}

table img {
    width: 50px;
    height: 50px;
    object-fit: cover;
}

table td button {
    border: none;
    background-color: transparent;
    cursor: pointer;
    font-size: 1.5rem;
    font-weight: 600;
    text-align: center;
}