.login {
    width: 100vw;
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background-image: url(./img/login_back.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.main {
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.logo {
    background: url(./img/logo.png);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    width: 60px;
    height: 60px;

    margin-bottom: 30px;
}

form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;
}

.user_icon {
    position: absolute;
    top: 9%;
    left: 10px;

    color: rgba(255, 255, 255, 0.6);

    font-size: 1.3rem;
}

.id {
    width: 330px;
    height: 45px;
    padding: 10px 40px;
    margin-bottom: 10px;

    background-color: rgba(255, 255, 255, 0.3);
    border: none;
    border-radius: 5px;
    box-sizing: border-box;

    color: white;

    font-size: 1.3rem;
}

.id::placeholder {
    color: rgba(255, 255, 255, 0.6);
    font-size: inherit;
}

.pass_icon {
    position: absolute;
    top: 36%;
    left: 10px;

    color: rgba(255, 255, 255, 0.6);

    font-size: 1.3rem;
}

.pw {
    width: 330px;
    height: 45px;
    padding: 10px 40px;
    margin-bottom: 10px;

    background-color: rgba(255, 255, 255, 0.3);
    border: none;
    border-radius: 5px;
    box-sizing: border-box;

    color: white;

    font-size: 1.3rem;
}

.pw::placeholder {
    color: rgba(255, 255, 255, 0.6);
    font-size: inherit;
}

.errortext {
    min-height: 24px;

    color: red;

    margin-bottom: 10px;
}

.loginbutton {
    width: 330px;
    height: 60px;

    background-color: rgba(255, 255, 255, 0.5);
    color: white;

    border: none;
    cursor: pointer;
    
    border-radius: 7px;

    font-size: 1.3rem;
}