.main {
    position: relative;

    width: 85vw;
    min-height: 100vh;

    background-color: lightgray;

    overflow: hidden; 
}


.headline {
    position: absolute;

    top : 5%;

    left: 5%;
    font-size: 40px;
}

.register {
    position: relative;

    top: 15vh;
    left: 5vw;

    width: 10vw;
    height: 5vh;

    background-color: lightblue;
    border: 1px solid black;
}
.search {
    position: absolute;
    display: flex;

    top: 15%;
    left: 63%;
    width: 100%;
    height: 5%;

}

.drop1, .drop2 {
    margin-right: 1%;
    width: 8%;

    background-color: white;

    padding: 5px;
}

.search_bar {
    position: relative;

    width: 15%;
    height: 100%;

    right: 0%;
    background-color: rgb(250, 250, 186);
}


.contentsboxlist1 {
    position: absolute;
    display: flex;

    margin-right: 5%;
    margin-left: 5%;    
    top: 25%;

    width: 100%;
    height: 100%;

}

.contentsboxlist2 {
    position: absolute;
    display: flex;

    top: 60%;
    margin-right: 5%;
    margin-left: 5%;    

    width: 100%;
    height: 100%;
}

.contentsbox {
    display: grid;
    margin-right: 4%;

    width: 20%;
    height: 30%;

    background-color: white;
}

.contentsbox1 p {

    color: black;
}

.contents_head1 {
    display: grid;

    background-color: rgb(211, 249, 96);
}

.contents_head2 {
    display: grid;

    background-color: rgb(255, 96, 255);
}
.img_box {
    position: relative;
    display: flex;

    margin: 5% 5% 0 5%;

    width: 90%;
    height: 70%;

    background-color: pink;
    
}

.img_box img {
    width: 100%;
    height: auto;
}

.state {
    position: absolute;

    right: 4%;
    bottom: 12%;

    width: 20%;
    
    background-color: navy;
    color: white;
}


.course_name {
    position: absolute;

    margin-top: 55%;
    margin-bottom: 0;

    text-align: left;

}

.date {
    margin-left: 5%;

    text-align: left;
}

.footer {
    display: flex;
    align-items: center;

    background-color: beige;
}

.instructorname {
    margin-left: 5%;
    width: 15%;
}

.instructor {
    
    width: 10%;

}

.enrollment {
    margin-left: 35%;
    width: 40%;
}